@font-face {
  font-family: IRANSans;
  font-style: normal;
  font-weight: normal;
  src: url(/static/media/IRANSansWeb.70a5f97c.eot);
  src: url(/static/media/IRANSansWeb.70a5f97c.eot?#iefix) format("embedded-opentype"),
     url(/static/media/IRANSansWeb.9d38733b.woff2) format("woff2"),
     url(/static/media/IRANSansWeb.50f4bba2.woff)
      format("woff"),
     url(/static/media/IRANSansWeb.244a401e.ttf)
      format("truetype");
}

*:not(i) {
  font-family: IRANSans !important;
  direction: rtl;
  text-align: right;
  /* font-weight: 300;
  line-height: 1.5em; */
}

#treelist * {
  text-align: left;
}

#anti-treelist * {
  text-align: right;
}

.loader {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-transform: -webkit-translate(-50%, -50%);
          transform: -webkit-translate(-50%, -50%);
  transform: -moz-translate(-50%, -50%);
  transform: -ms-translate(-50%, -50%);
}

.to-center {
  position: fixed;
  left: 50%;
}

.thumbnail {
  position: relative;
  width: 600px;
  height: 500px;
  border-radius: 10px;
  overflow: hidden;
}
.thumbnail img {
  position: absolute;
  left: 50%;
  top: 50%;
  height: 100%;
  width: auto;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.thumbnail-small {
  position: relative;
  width: 50px;
  height: 50px;
  border-radius: 5px;
  overflow: hidden;
}
.thumbnail-small img {
  position: absolute;
  left: 50%;
  top: 50%;
  height: 100%;
  width: auto;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.be-bold {
  font-weight: 600;
}

.neveshte-abi {
  color: #00acc1;
}

input {
  font-size: 20px !important;
}

.load {
  background: ghostwhite;
}

.react-autosuggest__container {
  position: relative;
}

.react-autosuggest__input {
  display: block;
  width: 100%;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15) !important;
}

.react-autosuggest__input--focused {
  outline: none;
}

.react-autosuggest__input--open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.react-autosuggest__suggestions-container {
  display: none;
}

.react-autosuggest__suggestions-container--open {
  display: block;
  position: absolute;
  top: 51px;
  width: 290px;
  border: 1px solid #ced4da;
  background-color: #fff;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border-radius: 0.25rem;
  max-height: 100px;
  overflow-y: auto;
  z-index: 2;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 10px 20px;
}

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd;
}

.react-autosuggest__suggestion-match {
  color: red;
  font-weight: bold;
}

@media print {
  .no-print {
    display: none;
  }
  .no-margin {
    margin: 0px !important;
    padding: 0px !important;
    left: 0px !important;
    right: 0px !important;
    top: 0px !important;
    bottom: 0px !important;
    background-color: #ffffff !important;
    box-shadow: #ffffff !important;
  }
}

